<template>
  <v-container class="mb-5">
    <v-app-bar app class="green" dark>
      <v-card-title class="headline">
        <img src="/favicon.ico" alt="Logo" class="logo-small">
        Haen Report
      </v-card-title>
      <v-toolbar-title class="ml-8 clickable" @click="navigateHome">Home</v-toolbar-title>
      <v-toolbar-title class="ml-8 clickable" @click="navigateDashboard" v-if="isAdmin">Dashboard</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn class="mr-8" color="orange" dark @click="logout">Log Out</v-btn>
    </v-app-bar>
    <v-row v-if="login">
      <v-col cols="12" sm="12" md="12" lg="4" v-for="menu in menus" :key="menu.title">
        <v-card class="menu-card" @click="navigate(menu.route)">
          <v-img :src="menu.icon" class="menu-icon"></v-img>
          <v-card-title class="menu-title">{{ menu.title }}</v-card-title>
        </v-card>
      </v-col>
    </v-row>
    <v-row v-if="!login">
      <!-- Tampilan jika tidak login -->
    </v-row>
  </v-container>
</template>

<script>
import Swal from "sweetalert2";

export default {
  name: "Home",
  data() {
    return {
      login: true,
      isAdmin : false,
      menus: [
        {
          title: "Absen Pagi",
          icon: "../Haen-cloudy/images/CheckIn.jpg",
          route: "/checkin"
        },
        {
          title: "Absen Sore",
          icon: "../Haen-cloudy/images/CheckOut.jpg",
          route: "/checkout"
        },
        {
          title: "Report Hafalan",
          icon: "../Haen-cloudy/images/AllList.jpg",
          route: "/hafalan"
        },
      ]
    };
  },
  methods: {
    navigate(route) {
      // Routing ke tujuan
      this.$router.push(route);
    },
    navigateDashboard() {
      this.$router.push("/dashboard");
    },
    navigateHome() {
      // Routing ke Home
      this.$router.push("/home");
    },
    logout() {
      Swal.fire({
        title: "Konfirmasi",
        text: "Anda yakin ingin keluar?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Keluar",
        cancelButtonText: "Batal"
      }).then(result => {
        if (result.isConfirmed) {
          // Log out dan routing ke LoginView
          // Clear session, hapus token, dll
          localStorage.clear()
          this.$router.push("/login");
        }
      });
    }
  },
  created(){
    if(localStorage.role){
      if(localStorage.role === "Admin"){
        this.isAdmin = true
      } else {
        this.isAdmin = false
      }
    } 
  }
  
}
</script>

<style scoped>
.menu-card {
  cursor: pointer;
  text-align: center;
  padding: 16px;
  margin-bottom: 16px;
}

.menu-icon {
  height: 200px;
}

.menu-title {
  margin-top: 16px;
}

.logo-small {
  width: 50px;
  /* Ubah ukuran sesuai kebutuhan */
  height: 50px;
  /* Ubah ukuran sesuai kebutuhan */
  display: block;
  margin: 0 auto;
}

.blue {
  background-color: blue;
  color: white;
}

.clickable {
  cursor: pointer;
}
</style>
